import axios from "axios";
import { useEffect, useState } from "react";
import Covids from "../../components/Covids";
import Hero from "../../components/Hero";
import Summary from "../../components/Summary";
import ENDPOINT from "../../utils/constants/endpoints";

export default function Indonesia() {
  const [covids, setCovids] = useState([]);

  useEffect(() => {
    fetchGlobalCovid();
  }, []);

  async function fetchGlobalCovid() {
    const response = await axios(ENDPOINT.INDONESIA);
    const covidData = [
      {
        status: "Confirmed",
        ...response.data.confirmed,
      },
      {
        status: "Deaths",
        ...response.data.deaths,
      },
      {
        status: "Recovered",
        ...response.data.recovered,
      },
    ];
    setCovids(covidData);
  }

  const summaryProps = {
    title: "Indonesia Summary",
    description: "Summary Data Indonesia",
    endpoint: ENDPOINT.INDONESIA,
  };

  const covidsProps = {
    title: "Indonesia",
    description: "Data Covid Berdasarkan Indonesia",
    covids,
  };

  return (
    <>
      <Hero />
      <Covids {...covidsProps} />
      <Summary {...summaryProps} />
    </>
  );
}
