const { createSlice } = require("@reduxjs/toolkit");
const { default: data } = require("../../utils/constants/provinces");

const provincesSlice = createSlice({
  name: "Provinces Slice",
  initialState: {
    provinces: data.provinces,
  },
  reducers: {
    updateProvinces(state, action) {
      const { provinces } = state;
      const { province, status, jumlah } = action.payload;

      const index = provinces.findIndex((item) => item.kota === province);
      const foundProvince = provinces.find((item) => item.kota === province);

      provinces[index] = {
        ...foundProvince,
        [status]: parseInt(foundProvince[status]) + parseInt(jumlah),
      };
    },
  },
});

const provincesReducer = provincesSlice.reducer;
export const { updateProvinces } = provincesSlice.actions;
export default provincesReducer;
