import { useSelector } from "react-redux";
import styles from "./index.module.css";

export default function TableBody() {
  const provinces = useSelector((state) => state.provinces.provinces);
  let angka = 1;

  const dataTable = provinces.map((province) => (
    <tr key={province.kota}>
      <td className={styles.table__row}>{angka++}</td>
      <td className={styles.table__row}>{province.kota}</td>
      <td className={styles.table__row}>{province.kasus}</td>
      <td className={styles.table__row}>{province.sembuh}</td>
      <td className={styles.table__row}>{province.meninggal}</td>
      <td className={styles.table__row}>{province.dirawat}</td>
    </tr>
  ));

  return <tbody>{dataTable}</tbody>;
}
