import axios from "axios";
import { useEffect, useState } from "react";
import Covids from "../components/Covids";
import Hero from "../components/Hero";
import Summary from "../components/Summary";
import ENDPOINT from "../utils/constants/endpoints";

export default function Home() {
  const [covids, setCovids] = useState([]);

  useEffect(() => {
    fetchGlobalCovid();
  }, []);

  async function fetchGlobalCovid() {
    const response = await axios(ENDPOINT.GLOBAL);
    const covidData = [
      {
        status: "Confirmed",
        ...response.data.confirmed,
      },
      {
        status: "Deaths",
        ...response.data.deaths,
      },
      {
        status: "Recovered",
        ...response.data.recovered,
      },
    ];
    setCovids(covidData);
  }

  const summaryProps = {
    title: "Global Summary",
    description: "Summary Data Global",
    endpoint: ENDPOINT.GLOBAL,
  };

  const covidsProps = {
    title: "Global",
    description: "Data Covid Berdasarkan Global",
    covids,
  };

  return (
    <>
      <Hero />
      <Covids {...covidsProps} />
      <Summary {...summaryProps} />
    </>
  );
}
