import CardCovid from "./Card";
import styles from "./index.module.css";

function Covids({ title, description, covids }) {
  return (
    <>
      <h2 className={styles.cards__title}>{title}</h2>
      <h4 className={styles.cards__description}>{description}</h4>
      <div className={styles.cards}>
        {covids.map((covid) => {
          return <CardCovid covid={covid} key={covid.status} />;
        })}
      </div>
    </>
  );
}

Covids.defaultProps = {
  title: "Global",
  description: "Data Covid",
};

export default Covids;
