import styles from "./index.module.css";

function CardCovid({ covid }) {
  return (
    <div className={styles.card}>
      <h3 className={styles.card__status}>{covid.status}</h3>
      <h4 className={`${styles.card__total} ${styles[covid.status]}`}>
        {covid.value.toLocaleString()}
      </h4>
      <a className={styles.card__detail} href={covid.detail}>
        More Info
      </a>
    </div>
  );
}

export default CardCovid;
