import { useState } from "react";
import styles from "./index.module.css";
import FormImage from "../../assets/img/Form.png";
import Alert from "../Alert";
import { useDispatch, useSelector } from "react-redux";
import { updateProvinces } from "../../features/provincesSlice";

export default function FormCovid() {
  const provinces = useSelector((state) => state.provinces.provinces);
  const dispatch = useDispatch();

  const [formData, setFormData] = useState({
    province: "Jakarta",
    status: "kasus",
    jumlah: 0,
    isSubmit: false,
  });
  const [errors, setErrors] = useState({
    isJumlahError: false,
  });

  function handleChange(e) {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  }

  function update() {
    dispatch(updateProvinces(formData));
    return true;
  }

  function validate() {
    setFormData({
      ...formData,
      isSubmit: false,
    });

    if (jumlah === 0 || jumlah === "") {
      setErrors({
        ...errors,
        isJumlahError: true,
      });
      return false;
    }

    setErrors({
      ...errors,
      isJumlahError: false,
    });
    return true;
  }

  function resetForm() {
    setFormData({
      province: "Jakarta",
      status: "kasus",
      jumlah: 0,
      isSubmit: true,
    });
  }

  function handleSubmit(e) {
    e.preventDefault();
    validate() && update() && resetForm();
  }

  const { province, status, jumlah, isSubmit } = formData;
  const { isJumlahError } = errors;

  return (
    <section className={styles.form_covid}>
      <div className={styles.form_covid__left}>
        <img
          className={styles.form_covid__image}
          src={FormImage}
          alt="Form Covid"
        />
      </div>
      <div className={styles.form_covid__right}>
        <h2 className={styles.form_covid__title}>Form Covid</h2>
        {isSubmit && (
          <p className={styles.form_covid__success}>Data Berhasil Ditambah</p>
        )}
        <form onSubmit={handleSubmit} className={styles.form}>
          <div className={styles.form__group}>
            <label htmlFor="provinces" className={styles.form__label}>
              Provinsi
            </label>
            <select
              name="province"
              id="province"
              className={styles.form__control}
              value={province}
              onChange={handleChange}
            >
              {provinces.map((province) => (
                <option key={province.kota} value={province.kota}>
                  {province.kota}
                </option>
              ))}
            </select>
          </div>
          <div className={styles.form__group}>
            <label htmlFor="status" className={styles.form__label}>
              Status
            </label>
            <select
              name="status"
              id="status"
              className={styles.form__control}
              value={status}
              onChange={handleChange}
            >
              <option value="kasus">Positif</option>
              <option value="sembuh">Sembuh</option>
              <option value="meninggal">Meninggal</option>
              <option value="dirawat">Dirawat</option>
            </select>
          </div>
          <div className={styles.form__group}>
            <label htmlFor="jumlah" className={styles.form__label}>
              Jumlah
            </label>
            <input
              id="jumlah"
              className={styles.form__control}
              type="number"
              value={jumlah}
              name="jumlah"
              onChange={handleChange}
            />
            {isJumlahError && (
              <Alert>Jumlah harus diisi atau lebih besar dari 0</Alert>
            )}
          </div>
          <div className={styles.form__group}>
            <button className={styles.form__button}>Submit</button>
          </div>
        </form>
      </div>
    </section>
  );
}
